@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i|Raleway:300,300i,400,400i,500,500i,600,600i,700,700i|Poppins:300,300i,400,400i,500,500i,600,600i,700,700i');

// Variables

$primaryColor: #fdba34;
$secondaryColor: #0C0A3E;
$lightGray: #ddd;
$offWhite: #efefef;
$mainTransition: all 0.3s linear;
$quickTransition: all 0.1s linear;
$mobileWidth: 768px;
$displayHeight: 80px;

// Globals

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  font-family: "Open Sans", sans-serif;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: $offWhite; }

// Calc

#calc {
  display: flex;
  flex-direction: column;
  // padding: 2em 1em
  min-width: 400px;
  height: 500px;
  overflow: hidden;
  box-shadow: 0 2.8px 7.5px rgba(0, 0, 0, 0.021),
  0 6.7px 15.5px rgba(0, 0, 0, 0.038),
  0 12.5px 25.1px rgba(0, 0, 0, 0.057),
  0 22.3px 38.6px rgba(0, 0, 0, 0.082),
  0 41.8px 62.5px rgba(0, 0, 0, 0.12),
  0 100px 131px rgba(0, 0, 0, 0.21) {} }

// Display

.display {
  background: black;
  color: white;
  height: $displayHeight;
  display: flex;
  flex-direction: column;
  padding: 0 20px;
  justify-content: space-evenly;

  div {
    text-align: right; } }

// Panel

#panel {
  display: grid;
  height: calc(100% - #{$displayHeight});
  grid-auto-flow: row dense;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-template-areas: "clear clear divide mult" "seven eight nine sub" "four five six add" "one two three equals" "zero zero decimal equals";

  button {
    background: $lightGray;
    color: black;
    border-radius: 0;
    border: none;
    transition: $quickTransition;

    &:active, &:focus {
      box-shadow: none; }

    &:hover {
      font-size: 1.5em;
      font-weight: 600; } }

  #clear {
    background: red;
    grid-area: clear; }

  #divide {
    grid-area: divide; }

  #multiply {
    grid-area: mult; }

  #subtract {
    grid-area: sub; }

  #add {
    grid-area: add; }

  #equals {
    grid-area: equals;
    background: green; }

  #decimal {
    grid-area: decimal; }

  #zero {
    grid-area: zero; }

  #one {
    grid-area: one; }

  #two {
    grid-area: two; }

  #three {
    grid-area: three; }

  #four {
    grid-area: four; }

  #five {
    grid-area: five; }

  #six {
    grid-area: six; }

  #seven {
    grid-area: seven; }

  #eight {
    grid-area: eight; } }
